// src/components/NavigationBar.js

import React from 'react';
import { NavLink as RRNavLink } from 'react-router-dom';
import { Navbar, NavbarBrand, Nav, NavItem, NavLink, NavbarToggler, Collapse } from 'reactstrap';
import './navigationbar.css';
class NavigationBar extends React.Component {
  constructor(props) {
    super(props);

    this.toggleNavbar = this.toggleNavbar.bind(this);
    this.state = {
      isOpen: false
    };
  }

  toggleNavbar() {
    this.setState({
      isOpen: !this.state.isOpen
    });
  }

  render() {
    return (

        <Navbar className='navbar' expand="md" fixed='top'>
          <NavbarBrand className='active' href="portfolio/">My Portfolio</NavbarBrand>
          <NavbarToggler onClick={this.toggleNavbar} />
          <Collapse isOpen={this.state.isOpen} navbar>
            <Nav className="ml-auto" navbar>
              <NavItem>
                <NavLink to="portfolio/"  activeClassName="active" tag={RRNavLink}>Dashboard</NavLink>
              </NavItem>
              <NavItem>
                <NavLink to="portfolio/about" activeClassName="active" tag={RRNavLink}>About</NavLink>
              </NavItem>
              <NavItem>
                <NavLink to="portfolio/projects" activeClassName="active" tag={RRNavLink}>Projects</NavLink>
              </NavItem>
              <NavItem>
                <NavLink to="portfolio/experience" activeClassName="active" tag={RRNavLink}>Experience</NavLink>
              </NavItem>
              <NavItem>
                <NavLink to="portfolio/contact" activeClassName="active" tag={RRNavLink}>Contact Us</NavLink>
              </NavItem>
            </Nav>
          </Collapse>
        </Navbar>
    );
  }
}

export default NavigationBar;
